.day{box-sizing:border-box;flex:1 0 auto;min-width:33.33%;color:#E0E0E0;border-bottom:0.0625rem solid currentColor}.day--hoverable:hover{background:#E5E5E5;color:#BFBFBF;z-index:99}.day--hoverable:hover .day__name{background:#E0E0E0}.day:first-child,.day:last-child{background-repeat:no-repeat;background-size:20px 100%}.day:first-child{background-image:linear-gradient(to right, #fff 50%, rgba(255,255,255,0) 100%)}.day:last-child{background-image:linear-gradient(to left, #fff 50%, rgba(255,255,255,0) 100%);background-position:100% 0}.day__name{color:#333;text-transform:capitalize;display:flex;justify-content:center;align-items:center;min-height:3em}.day__hours{display:flex;padding-left:2px;border-left:0.0625rem solid currentColor}.hours__row{margin-left:1px;flex:1 1 auto}.row__hour{box-sizing:border-box;border-top:0.0625rem solid currentColor;height:5em}@media (min-width: 768px){.day{min-width:20%}}@media (min-width: 1280px){.day{flex:0 0 auto;display:flex;border-bottom:0}.day__name{flex:0 0 5%;min-height:5.5em}.day__hours{flex:1 1 auto;display:flex;padding:0;flex-direction:column;border:0.0625rem solid currentColor;border-right:0;margin-bottom:-1px}.day:last-child .day__hours{margin-bottom:0}.hours__row{display:flex;flex:1 0 auto}.hours__row:first-child .row__hour{padding-top:0.125em}.row__hour{border-right:0.0625rem solid currentColor;padding-left:0;height:auto;flex:1 0 0%;border-top:0}}



