.timetable{width:100%;margin:auto}.timings{float:left;width:3em;margin:2.2em 0 0 0}.timings__hour{text-align:center;white-space:nowrap;height:5em}.timings__hour small{color:#555}.timings__hour:last-child{height:1em}.week{width:calc(100% - 3em);overflow-x:auto;-webkit-overflow-scrolling:touch;display:flex;background:radial-gradient(ellipse at left, rgba(0,0,0,0.2) 0%, transparent 75%) 0 center,radial-gradient(ellipse at right, rgba(0,0,0,0.2) 0%, transparent 75%) 100% center;background-size:10px 125%, 10px 125%;background-attachment:scroll, scroll;background-repeat:no-repeat;overflow-y:hidden}@media (min-width: 768px){.week{background:none}}@media (min-width: 1280px){.timetable{flex-direction:column;height:auto;max-width:77%}.timetable--expanded{padding-right:2.5%;max-width:95%}.timings{float:none;position:relative;left:-1.2%;width:102.2%;display:flex;border-right:0;justify-content:space-around}.timings--expanded{left:0%;width:101%}.timings__hour{flex:1 0 0%;text-align:right;height:3em;line-height:4.5em}.week{width:100%;flex-direction:column}}



